.App {
  text-align: center;
  padding: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin: 20px 0;
}

table, th, td {
  border: 1px solid black;
}

th, td {
  padding: 10px;
  text-align: left;
  max-width: 150px; /* カラムの最大幅を設定 */
}

.App p {
  font-weight: bold;
}

input, select {
  /* width: 100%; */
  box-sizing: border-box;
}

.App select {
  float: left;
}

.App select option {
  width: 10%;
}
